@import "../../utils/commons.scss";

.container {
  padding: 1rem 0;
  min-height: calc(100vh - 100px);

  .hero {
    margin: 2rem auto;
    h2 {
      font-size: 2rem;
      font-weight: 600;
      text-align: center;
      div {
        display: inline-block;
      }
    }
    p {
      text-align: center;
    }
  }

  .featureCards {
    position: relative;
    width: 100%;
    height: 450px;
    // margin: 0 auto;
    margin-top: 5rem;
    background: url("/assets/bg-home.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    .card {
      padding: 1rem;
    }
  }

  .portfolio {
    width: 100%;
    background: rgba(0, 0, 0, 1);
    background-image: url(../../assets/bg/portfolio-pattern.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position-x: -8%;
    padding: 2rem 1rem;

    h1 {
      font-size: 2rem;
      color: white;
      text-align: center;
    }

    .projects {
      margin: 2rem calc((100% - var(--fixed-width)) / 2);
      @extend .flexRow;
      flex-wrap: wrap;

      .project {
        height: 200px;
        width: 200px;
        border-radius: 1rem;
        overflow: hidden;
        position: relative;
        border: 2px solid white;
        margin: 1rem;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        .content {
          position: absolute;
          bottom: -50px;
          left: 0;
          width: 100%;
          background: rgba(0, 0, 0, 0.8);
          padding: 0 0.5rem;
          height: 50px;
          h3 {
            color: white;
            font-size: 1rem;
            font-weight: 600;
          }

          transition: 0.3s ease-in-out;
        }

        &:hover {
          .content {
            bottom: 0;
          }
        }
      }
    }
  }

  .clients {
    padding: 2rem 0;
    h1 {
      font-size: 2rem;
      text-align: center;
    }
    p {
      text-align: center;
    }

    .clientsSlider {
      background: rgba(0, 0, 0, 0.07);
      padding: 1rem 0;
      margin-top: 1rem;

      .client {
        @extend .flexRow;
        display: flex !important;
        align-items: center !important;
        cursor: pointer;
        transition: 0.2s ease-out;
        transition-property: background;
        border-radius: 1rem;
        img {
          width: 250px;
        }

        &:hover {
          background: rgba(0, 0, 0, 0.1);
        }
      }
    }
  }
}
